.text{
    border-radius: 10px;
    background-color: #1F2833;
    width: 500px;
    font-family: Georgia, serif;
    margin: 0 auto;
    font-size: 16px;
    display: flex;
    justify-content: center;
    color:#66FCF1 ;
}

.button{
    border-color: black;
    border-radius: 10px;
    background-color: #66FCF1;
    color:#1F2833;
    margin:15px;
    width: 300px;
    height: 45px;
    font-size: 30px;
    font-weight: 600;
}

.buttons{
    display: flex;
    margin: 20px auto 0 auto;
    flex-direction: column;
}

.quantity{
    display: flex;
    justify-content: center;
    flex-direction: column;
}